import { Box, Heading, Text, Flex, Link as ChakraLink } from "@chakra-ui/react";
import Link from "next/link";

export default function HomePage() {
  return (
    <Flex 
      minH="100vh" 
      align="center" 
      justify="center" 
      bg="gray.50" 
      _dark={{ bg: "gray.900" }}
    >
      <Box textAlign="center" p={8} borderRadius="xl" bg="white" _dark={{ bg: "gray.800" }} boxShadow="sm">
        <Heading size="2xl" mb={4}>Welcome to the App</Heading>
        <Text color="gray.600" _dark={{ color: "gray.400" }}>
          Your dashboard is under construction.
        </Text>
        <Text mt={6}>
          Please{" "}
          <ChakraLink asChild color="teal.500" fontWeight="bold">
            <Link href="/login">sign in</Link>
          </ChakraLink>
          {" "}to continue.
        </Text>
      </Box>
    </Flex>
  );
}